<style>
@import '../../../../../static/style/file_upload.css';
.ll-p-m-w {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}
.ll-img-w {
  width: 60px;
  height: 60px;
}
.ll-img-w > img {
  width: 60px;
}
.ll-p-m-c-r {
  padding: 0 10px;
}
.ll-erp-p-no {
  color: #666;
  font-size: 12px;
}
.del-store-icon-w {
  position: absolute;
  top: -8px;
  right: 2px;
  cursor: pointer;
}
.ll-p-m-w img {
  width: 110rpx;
}
.c-g-sfw {
  padding-top: 0 !important;
  padding-bottom: 0 !important;
}
.choose-goods-wrap {
  border: 1px solid #ececec;
  border-radius: 4px;
}
.store-pre-time-item-wrap {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
}
.store-pre-time-item-wrap > .item-left {
  flex: 0 0 90px;
  width: 90px;
  text-align: left;
  margin-left: 20px;
}
.store-pre-time-item-wrap > .item-right {
  border: 1px solid #ececec;
  border-radius: 8px;
  padding: 8px;
  width: 550px;
  box-sizing: border-box;
}
</style>
<template>
  <div>
    <div class="add-form-wraper">
      <el-form ref="form" :model="addParamMesg" label-width="140px">
        <el-form-item label="活动类型" required>
          <p
            style="color: #222;font-size: 16px; font-weight: bold;"
          >{{addParamMesg.activityTypeName}}</p>
        </el-form-item>
        <region className="add-input" label="选择区域" :required="true"></region>
        <el-form-item label="活动名称" required>
          <el-input
            v-model="addParamMesg.activityName"
            size="small"
            class="add-input"
            placeholder="请输入活动名称"
            clearable
          ></el-input>
          <span>活动名称,限制30个汉字</span>
        </el-form-item>
        <el-form-item label="开始时间" required>
          <el-date-picker
            class="add-input"
            v-model="addParamMesg.activityStartDate"
            type="datetime"
            placeholder="请选择活动开始时间"
            format="yyyy-MM-dd HH:mm:ss"
            value-format="yyyy-MM-dd HH:mm:ss"
            size="small"
            :picker-options="pickerOptions"
          ></el-date-picker>
          <span>活动开始时间</span>
        </el-form-item>
        <el-form-item label="结束时间" required>
          <el-date-picker
            class="add-input"
            v-model="addParamMesg.activityEndDate"
            type="datetime"
            placeholder="请选择活动结束时间"
            format="yyyy-MM-dd HH:mm:ss"
            value-format="yyyy-MM-dd HH:mm:ss"
            size="small"
            :picker-options="pickerOptions"
          ></el-date-picker>
          <span>活动结束时间</span>
        </el-form-item>
        <el-form-item label="门店/履约时间" required>
          <div
            class="store-pre-time-item-wrap"
            :class="index > 0 ? 'mt10' : null"
            v-for="(item, index) of activityStoreGroupList"
            :key="index"
          >
            <div class="item-right">
              <div>
                <span>参与门店</span>
                <el-button size="small" @click="chooseStoreOpen(index)" icon="el-icon-plus">选择门店</el-button>
                <div v-if="item.activityStoresCheckedList.length > 0">
                  <el-table
                    class="fixed-height"
                    :data="item.activityStoresCheckedList"
                    empty-text="暂无选中任何门店"
                    max-height="250"
                    size="small"
                  >
                    <el-table-column min-width="220px" label="门店编码/名称">
                      <template slot-scope="scope">
                        <div>{{scope.row.erpStoreNo}}_{{scope.row.erpStoreName}}</div>
                      </template>
                    </el-table-column>
                    <el-table-column min-width="80" prop="cityName" label="城市"></el-table-column>
                    <el-table-column min-width="80" prop="saleTypeName" label="门店类型"></el-table-column>
                    <el-table-column align="center" label="操作" width="100">
                      <template slot-scope="scope" size="small">
                        <el-button
                          @click="deleteStoreItem(index, scope.row)"
                          type="text"
                          size="mini"
                          icon="el-icon-delete"
                        >删除</el-button>
                      </template>
                    </el-table-column>
                  </el-table>
                </div>
              </div>
              <div class="mt10">
                <span>履约时间</span>
                <!-- default-time="23:59:59"
                format="yyyy-MM-dd HH:mm:ss"
                value-format="yyyy-MM-dd HH:mm:ss"-->
                <el-date-picker
                  class="search-input"
                  v-model="item.performanceDate"
                  type="date"
                  placeholder="请选择履约时间"
                  format="yyyy-MM-dd"
                  value-format="yyyy-MM-dd"
                  size="small"
                  :picker-options="pickerOptions"
                ></el-date-picker>
                <span>门店仅支持自提则为自提时间</span>
              </div>
            </div>
            <div class="item-left">
              <el-button
                type="primary"
                size="mini"
                icon="el-icon-plus"
                circle
                @click="doAddStoreGroupHandel"
              ></el-button>&nbsp;
              <el-button
                size="mini"
                icon="el-icon-minus"
                circle
                v-if="activityStoreGroupList.length > 1"
                @click="doMinusStoreGroupHandel(index)"
              ></el-button>
            </div>
          </div>
        </el-form-item>
        <el-form-item label="活动商品" required>
          <el-button type="primary" @click="goAddGoodsDialog" size="mini" icon="el-icon-plus">添加商品</el-button>
          <el-button @click="showExcelBatchInsertDialog" size="mini" icon="el-icon-plus">Excel批量导入</el-button>&nbsp;&nbsp;&nbsp;&nbsp;
          <span style="color: red;">*注意：</span>
          <span>商品图片建议上传大小不超过300K的710*420像素的jpg或png图片</span>
          <el-table
            class="fixed-height choose-goods-wrap"
            :data="checkedList"
            empty-text="暂无选中任何商品"
            style="width:100%;"
            max-height="560"
            size="small"
          >
            <el-table-column min-width="300px" label="商品信息">
              <template slot-scope="scope">
                <div class="ll-p-m-w">
                  <div class="mh-up-w" @click="setItemIdx(scope.row.goodsNo)">
                    <el-upload
                      class="avatar-uploader-sf-goods-img"
                      action
                      :show-file-list="false"
                      :before-upload="imageUploadReg"
                      :http-request="emitUpload"
                    >
                      <div v-loading="imageUploadLoading && currentUpFileIdx == scope.row.goodsNo">
                        <span v-if="scope.row.preSaleImageUrl">
                          <img :src="scope.row.preSaleImageUrl" class="avatar-sf-goods" />
                        </span>
                        <i v-else class="el-icon-plus avatar-uploader-sf-icon"></i>
                      </div>
                    </el-upload>
                  </div>
                  <div class="ll-p-m-c-r">
                    <div>
                      {{scope.row.goodsName || '--'}}
                    </div>
                    <div class="ll-erp-p-no">SKU编码：{{scope.row.goodsNo || '--'}}</div>
                    <div class="ll-erp-p-no">商品ID：{{scope.row.platformCityGoodsId || '--'}}</div>
                    <div class="ll-erp-p-no">商品编码：{{scope.row.goodsId || '--'}}</div>
                  </div>
                </div>
              </template>
            </el-table-column>
            <el-table-column min-width="100px" prop="spec" label="规格"></el-table-column>
            <el-table-column min-width="130px" label="活动商品名称">
              <template slot-scope="scope">
                <div>
                  <el-input v-model="scope.row.activityGoodsName" size="small" placeholder="输入名称"></el-input>
                </div>
              </template>
            </el-table-column>
            <el-table-column min-width="130px" label="原价(必填)">
              <template slot-scope="scope">
                <div>
                  <el-input v-model="scope.row.price" size="small" placeholder="输入原价"></el-input>
                </div>
              </template>
            </el-table-column>
            <el-table-column min-width="130px" label="活动价(必填)">
              <template slot-scope="scope">
                <div>
                  <el-input v-model="scope.row.groupPrice" size="small" placeholder="输入活动价"></el-input>
                </div>
              </template>
            </el-table-column>
            <el-table-column min-width="130px" label="活动库存(选填)">
              <template slot-scope="scope">
                <div>
                  <el-input v-model="scope.row.curStock" size="small" placeholder="门店限售库存"></el-input>
                </div>
              </template>
            </el-table-column>
            <el-table-column min-width="140px" label="每人限购">
              <template slot-scope="scope">
                <div>
                  <el-input v-model="scope.row.limitGoodsCount" size="small" placeholder="每人限购"></el-input>
                </div>
              </template>
            </el-table-column>
            <el-table-column min-width="140px" label="排序权重">
              <template slot-scope="scope">
                <div>
                  <el-input v-model="scope.row.sortNum" size="small" placeholder="排序（0-299越大越靠前）"></el-input>
                </div>
              </template>
            </el-table-column>
            <el-table-column min-width="190px" :render-header="sfInStockHeader">
              <template slot-scope="scope">
                <div>
                  <el-radio v-model="scope.row.sfInStock" :label="true">是</el-radio>
                  <el-radio v-model="scope.row.sfInStock" :label="false">否</el-radio>
                </div>
              </template>
            </el-table-column>
            <el-table-column fixed="right" align="center" label="操作" width="80">
              <template slot-scope="scope" size="small">
                <el-button
                  @click="deleteItem(scope.row)"
                  type="text"
                  size="mini"
                  icon="el-icon-delete"
                >删除</el-button>
              </template>
            </el-table-column>
          </el-table>
          <p>
            <span style="color:red;">*</span>活动未发布时可以编辑商品，发布后只可添加新商品，不可再编辑已有商品；
          </p>
          <p>
            <span style="color:red;">*</span>活动进行中时新上门店，请在活动商品中发布至门店；
          </p>
        </el-form-item>
        <hr class="s-slip-tb" />
        <el-form-item>
          <el-button type="primary" size="small" @click="doSaveHandel" :disabled="isDisabled">保存</el-button>
          <el-button size="small" @click="doCancelHandel">取消</el-button>
        </el-form-item>
      </el-form>
    </div>

    <!-- 选择门店 -->
    <choose-store ref="chooseStore" @confirmChooseStore="confirmChooseStore"></choose-store>
    <!-- 批量添加模板 -->
    <excel-add ref="excelAdd" @doConfirmHandel="doConfirmHandel"></excel-add>
    <!-- 选择商品 -->
    <choose-goods ref="chooseGoods" @confirmChooseGoods="confirmChooseGoods"></choose-goods>
    <el-dialog 
      title="批量导入失败" 
      :visible.sync="showTipErrorMsg" 
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      width='450px'>
      <div>
        部分商品不符合要求，请按要求修改后再重新导入。<el-button type="text" size="mini" @click="downLoadErrorMsg">下载查看失败原因</el-button>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="showTipErrorMsg = false" size="mini">关 闭</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import Storage from '@/utils/localStorage'
import ChooseStore from "@/components/Activity/Common/ChooseStore"
import ChooseGoods from "@/components/Activity/Common/ChooseGoods"
import AddByExcel from "@/components/Common/Excel/AddByExcel"
import exportSheet from '@/utils/exportExcel'
import Region from '@/components/Common/StoreCommon/Region'
import * as utils from '@/utils/utils'
import * as qiniu from 'qiniu-js'
const fileUrlList = {
  getQiNiuToken: '/foundation/qiniu/auth/file'
}
const reqUrl = {
  // 校验新增预售商品
  checkPreGoodsInsert: '/newretail-shop/a/preSale/goods/checkPreGoodsInsert',
  // 新增活动规则
  add: "/plateform/a/activityRule/add"
}
const numReg = /^[1-9]*[1-9][0-9]*$/
const salePriceReg = /((^[1-9]\d*)|^0)(\.\d{0,2}){0,1}$/
const salePriceOnePointReg = /((^[1-9]\d*))(\.\d{0,1}){0,1}$/
export default {
  data () {
    return {
      pickerOptions: {
        disabledDate (time) {
          return time.getTime() < Date.now() - 8.64e7; // 如果没有后面的-8.64e7就是不可以选择今天的
        }
      },
      isDisabled: false,
      addParamMesg: {
        DIFFCITY: true,
        platformId: 5,
        regionCode: Storage.get('regionCode') || '', // 区域ID
        activityType: 61, // 活动类型
        activityTypeName: '每日爆款', // 活动类型名称
        activityName: '', // 活动名称
        activityStartDate: '', // 活动开始时间
        activityEndDate: '', // 活动结束时间
        activityCode: '', // 活动编码
        // 参与门店 秒杀中此字段没用，但是后端有非空验证，所以希望前端加上，故此处把所有门店分组中的门店编号传入
        activityStoreList: [],
        // 活动门店分组集合
        activityStoreGroupList: []
      },
      // 选中的商品列表
      checkedList: [],
      // 商品详情图加载
      imageUploadLoading: false,
      currentUpFileIdx: -1, // 默认选中的文件上传框
      // 参与门店与履约时间
      activityStoreGroupList: [
        {
          performanceDate: '', // 履约时间
          activityStoresCheckedList: [] // 选中的门店
        }
      ],
      activityStoresCheckedList: [],
      tempConfig: {
        platformCityGoodsId: '区域商品id(必填)',
        goodsNo: 'SKU编码(选填)',
        skuNo: 'ERP商品编码(选填)',
        activityGoodsName: '活动商品名称(可修改，必填)',
        price: '原价(必填）',
        preSalePrice: '活动价(必填)',
        platformCurStock: '活动库存（选填，为空时不限）',
        limitCount: '每人每期限购(选填，为空时不限)',
        sortNum: '排序(必填)',
        sfInStockStr: '顺丰仓现货（选填，填是/否，空时为是）'
      },
      // 显示下载含有失败原因的excel
      showTipErrorMsg: false,
      // 后台返回的又失败原因的内容
      hasErrorMsgData: []
    }
  },
  components: {
    "excel-add": AddByExcel,
    "choose-store": ChooseStore,
    "choose-goods": ChooseGoods,
    "region": Region
  },
  methods: {
    /**
     * [chooseStoreOpen 选择门店]
     * @return {[type]} [description]
     */
    chooseStoreOpen (groupIndex) {
      let _params = {
        title: '选择门店',
        platformId: this.addParamMesg.platformId,
        regionCode: this.addParamMesg.regionCode,
        groupIndex: groupIndex,
        checkedList: this.activityStoreGroupList[groupIndex].activityStoresCheckedList,
        otherCheckedList: this.getCheckedStores(groupIndex)
      }
      this.$refs.chooseStore.setDefault(_params)
    },
    showExcelBatchInsertDialog () {
      if (this.$refs.excelAdd) {
        let _val = {
          title: 'Excel 批量导入',
          tempConfig: this.tempConfig
        }
        this.$refs.excelAdd.setDefaultMsg(_val)
      }
    },

    /**
     * [goAddGoodsDialog 添加商品]
     * @return {[type]} [description]
     */
    goAddGoodsDialog () {
      if (this.$refs.chooseGoods) {
        let _params = {
          title: '选择商品',
          regionCode: this.addParamMesg.regionCode,
          checkedList: this.checkedList
        }
        this.$refs.chooseGoods.setDefault(_params)
      }
    },
    /**
     * [confirmChooseStore 选中的门店]
     * @param  {[type]} _value [description]
     * @return {[type]}        [description]
     */
    confirmChooseStore (_value) {
      let activityStoresCheckedList = _value.checkedList || []
      let groupIndex = _value.groupIndex || 0
      this.activityStoreGroupList[groupIndex].activityStoresCheckedList = activityStoresCheckedList
    },
    /**
     * [confirmChooseGoods 选中商品并关闭]
     * @param  {[type]} val [description]
     * @return {[type]}      [description]
     */
    confirmChooseGoods (val) {
      this.checkedList = val.checkedList && val.checkedList.length ? val.checkedList : []
    },
    /**
   * [deleteItem 删除]
   * @param  {[type]} row [description]
   * @return {[type]}      [description]
   */
    deleteItem (row) {
      let platformCityGoodsId = row.platformCityGoodsId
      let resCheckedListArr = this.checkedList.filter(item => {
        return item.platformCityGoodsId != platformCityGoodsId
      })
      this.checkedList = JSON.parse(JSON.stringify(resCheckedListArr))
    },
    /**
     * [deleteStoreItem 删除选中的门店]
     * @param  {[type]} row [description]
     * @return {[type]}      [description]
     */
    deleteStoreItem (groupIndex, row) {
      let erpStoreNo = row.erpStoreNo
      let checkdStoreList = this.activityStoreGroupList[groupIndex].activityStoresCheckedList

      this.activityStoreGroupList[groupIndex].activityStoresCheckedList = checkdStoreList.filter(item => {
        return item.erpStoreNo !== erpStoreNo
      })
    },
    /**
     * @description: 设置当前上传图片的索引
     * @param {type} 
     * @return: 
     */
    setItemIdx (idx) {
      if (this.imageUploadLoading) return
      this.currentUpFileIdx = idx
    },
    /**
     * [emitUpload 上传图片]
     * @param  {[type]} e [description]
     * @return {[type]}   [description]
     */
    emitUpload (e) {
      this.imageUploadLoading = true
      this.getToken().then((res) => {
        // 开始上传图片
        let uploader = qiniu.upload(e.file, res.fileName, res.token)
        uploader.subscribe({
          error: () => {
            this.imageUploadLoading = false
          },
          complete: (res) => {
            // 添加内容设置图片地址
            let urlImage = 'http://retailimg01.benlailife.com/' + res.key
            this.imageUploadLoading = false
            // 设置对应的图片地址
            let checkedListArr = [...this.checkedList]
            checkedListArr.forEach(item => {
              if (item.goodsNo === this.currentUpFileIdx) {
                item.preSaleImageUrl = urlImage
              }
            })
            this.checkedList = checkedListArr
          },
          next: res => {
          }
        })
      })
    },

    /**
     * [imageUploadReg 上传图片检测]
     * @param  {[type]} file [description]
     * @return {[type]}      [description]
     */
    imageUploadReg (file) {
      const isPic = file.type.indexOf('image/') !== -1
      const isJPG = file.type === 'image/jpeg' || 'image/png'
      const isSizeNum = file.size <= 1 * 300 * 1024
      if (!isPic || !isJPG || !isSizeNum) {
        this.$message.error('商品图片建议上传大小不超过300K的710*420像素的jpg或png图片')
      }
      return isPic && isJPG && isSizeNum
    },
    /**
     * [getToken 通过后台封装的接口获取七牛的token]
     * @return {[type]}            [description]
     */
    getToken () {
      return this.$http.post(fileUrlList.getQiNiuToken, {}).then(res => {
        if (res.code == 0) {
          let token = res.data.token
          let fileName = res.data.key
          return { fileName, token }
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.imageUploadLoading = false
        this.$notify.error({
          title: '获取七牛token提示',
          message: e.message
        })
      })
    },

    /**
     * [doSaveHandel 保存]
     * @return {[type]} [description]
     */
    doSaveHandel () {
      if (!this.addParamMesg.activityName) {
        this.$message("请输入活动名称")
        return
      } else if (this.addParamMesg.activityName.length > 30) {
        this.$message("活动名称限制30个汉字")
        return
      }
      if (!this.addParamMesg.activityStartDate) {
        this.$message("请输入活动开始时间")
        return
      }
      if (!this.addParamMesg.activityEndDate) {
        this.$message("请输入活动结束时间")
        return
      }
      let activityStartDateNum = Date.parse(new Date(this.addParamMesg.activityStartDate.replace(/\-/g, '/')))
      let activityEndDateNum = Date.parse(new Date(this.addParamMesg.activityEndDate.replace(/\-/g, '/')))
      let currentDateNum = new Date().getTime()
      if (activityStartDateNum < currentDateNum) {
        this.$message("活动开始时间不能早于当前时间")
        return
      }
      if (activityStartDateNum > activityEndDateNum) {
        this.$message("活动结束时间不能早于开始时间")
        return
      }
      this.addParamMesg.activityStoreGroupList = this.activityStoreGroupList.map((i, idx) => {
        return {
          index: idx,
          performanceDate: i.performanceDate,
          storeNoList: i.activityStoresCheckedList.map(k => {
            return k.erpStoreNo
          })
        }
      })
      for(let item of this.addParamMesg.activityStoreGroupList) {
        if(!item.storeNoList || !item.storeNoList.length) {
          this.$message(`请选择参与门店/履约时间组中【第${item.index + 1}组】的参与门店`)
          return
        }
        if(item.performanceDate) {
          let performanceDateNum = Date.parse(new Date(`${item.performanceDate} 23:59:59`.replace(/\-/g, '/')))
          if(activityEndDateNum > performanceDateNum) {
            this.$message(`参与门店/履约时间组中【第${item.index + 1}组】的履约时间不能早于活动结束时间`)
            return
          }
          item.performanceDatetime = `${item.performanceDate} 23:59:59`
        }else {
          this.$message(`请选择参与门店/履约时间组中【第${item.index + 1}组】的履约时间`)
          return
        }
      }
      this.addParamMesg.activityStoreList = this.getCheckedStores().map(item => {
        return item.erpStoreNo
      })
      let goodsReqListArr = []
      for (let item of this.checkedList) {
        if(!item.preSaleImageUrl) {
          this.$message(`请上传(${item.goodsName})的商品图片`)
          return 
        }
        if (!item.activityGoodsName) {
          this.$message(`请输入(${item.goodsName})的活动商品名称`)
          return
        }
        if (!item.price && parseFloat(item.price) != 0) {
          this.$message(`请输入(${item.goodsName})的原价`)
          return
        } else {
          if (!salePriceReg.test(item.price) || item.price == 0) {
            this.$message('原价是大于0，小数保留小数点后2位')
            return
          }
        }
        if (!item.groupPrice && parseFloat(item.groupPrice) != 0) {
          this.$message(`请输入(${item.goodsName})的活动价`)
          return
        } else {
          if (!salePriceReg.test(item.groupPrice) || item.groupPrice == 0) {
            this.$message('活动价是大于0，小数保留小数点后2位')
            return
          }
        }
        if (parseFloat(item.groupPrice) >= parseFloat(item.price)) {
          this.$message(`(${item.goodsName})的原价必须大于活动价`)
          return
        }
        if (item.curStock != 0 && item.curStock) {
          if (!numReg.test(item.curStock)) {
            this.$message(`(${item.goodsName})的活动库存为大于零的正整数`)
            return
          }
        }
        if (item.limitGoodsCount != 0 && item.limitGoodsCount) {
          if (numReg.test(item.limitGoodsCount)) {
            if (item.limitGoodsCount < 0) {
              this.$message(`(${item.goodsName})的限购数量不能小于0`)
              return
            }
          } else {
            this.$message(`(${item.goodsName})的限购数量格式不正确`)
            return
          }
        }
        if (item.sortNum != 0 && item.sortNum) {
          if (numReg.test(item.sortNum)) {
            if (item.sortNum < 0 || item.sortNum > 299) {
              this.$message(`(${item.goodsName})的排序权重为0 — 299的整数，越大越靠前`)
              return
            }
          } else {
            this.$message(`(${item.goodsName})的排序权重为0 — 299的整数，越大越靠前`)
            return
          }
        }
        let goodsReqListItem = {
          preSaleImageUrl: item.preSaleImageUrl,
          goodsId: item.goodsId,
          goodsNo: item.goodsNo,
          skuNo: item.skuNo,
          goodsName: item.goodsName,
          activityGoodsName: item.activityGoodsName,
          price: item.price,
          groupPrice: item.groupPrice,
          curStock: item.curStock,
          limitGoodsCount: item.limitGoodsCount,
          sortNum: item.sortNum,
          spec: item.spec,
          platformCityGoodsId: item.platformCityGoodsId,
          sfInStock: item.sfInStock
        }
        goodsReqListArr.push(goodsReqListItem)
      }
      if (goodsReqListArr.length === 0) {
        this.$message("请先添加活动商品")
        return
      }
      this.addParamMesg.platCityGoodsInfos = goodsReqListArr
      this.isDisabled = true
      this.$http.post(reqUrl.add, { ...this.addParamMesg }).then(res => {
        // 保存按钮可再次点击
        setTimeout(() => {
          this.isDisabled = false
        }, 800)
        if (res.code === 0) {
          this.$message.success("保存成功")
          this.$router.push('/store_act')
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.isDisabled = false
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    },
    doCancelHandel () {
      this.$confirm('确定取消吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$router.push('/store_act')
      }).catch(() => { })
    },
    /**
     * @description: 添加分组
     * @param {type} 
     * @return: 
     */
    doAddStoreGroupHandel () {
      let groupItem = {
        performanceDate: '',
        activityStoresCheckedList: []
      }
      this.activityStoreGroupList.push(groupItem)
    },
    doMinusStoreGroupHandel (index) {
      this.activityStoreGroupList.splice(index, 1)
    },
    sfInStockHeader() {
      return (
        <el-popover
          placement="top-start"
          width="260"
          trigger="hover">
          <div>
            <p>1、顺丰标品设为顺丰仓现货时，下单会校验仓库库存；</p>
            <p>2、本来鲜生鲜品无需设置；</p>
          </div>
          <span slot="reference">是否为顺丰仓现货<i class="el-icon-question"></i></span>
        </el-popover>
      )
    },
    /**
     * @description: 导入excel后确认框
     * @param {type} 
     * @return: 
     */
    doConfirmHandel (value) {
      let excelDataList = value.dataList || []
      excelDataList.forEach(item => {
        item.activityType = 61
      })
      this.$http.post(reqUrl.checkPreGoodsInsert, [...excelDataList]).then(res => {
        this.$refs.excelAdd.closeDisabled()
        this.$refs.excelAdd.closeDialog()
        if (res.code === 0) {
          this.hasErrorMsgData = res.data && res.data.length ? res.data : []
          this.hasErrorMsgData.forEach(item => {
            item.groupPrice = item.preSalePrice
            item.curStock = item.platformCurStock
            item.limitGoodsCount = item.limitCount
          })
          let isOk = this.hasErrorMsgData.some(item => {
            return item.msgType === 1
          })
          if (isOk) {
            this.showTipErrorMsg = true
          } else {
            let checkedListTemp = [...this.checkedList]
            checkedListTemp.forEach(i => {
              this.hasErrorMsgData.forEach(j => {
                if (i.goodsId === j.goodsId && i.goodsNo === j.goodsNo && i.platformCityGoodsId === j.platformCityGoodsId && i.skuNo === j.skuNo) {
                  i.activityGoodsName = j.activityGoodsName
                  i.preSaleImageUrl = j.preSaleImageUrl
                  i.price = j.price
                  i.groupPrice = j.groupPrice
                  i.curStock = j.curStock
                  i.limitGoodsCount = j.limitGoodsCount
                  i.sortNum = j.sortNum
                  i.sfInStock = j.sfInStock
                }
              })
            })
            let hash = {}
            this.checkedList = [...checkedListTemp, ...this.hasErrorMsgData].reduceRight((item, next) => {
              hash[next.platformCityGoodsId] ? '' : hash[next.platformCityGoodsId] = true && item.push(next)
              return item
            }, [])
          }
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.$refs.excelAdd.closeDisabled()
        this.$refs.excelAdd.closeDialog()
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    },
    showExcelBatchInsertDialog () {
      if (this.$refs.excelAdd) {
        let _val = {
          title: 'Excel 批量导入',
          tempConfig: this.tempConfig
        }
        this.$refs.excelAdd.setDefaultMsg(_val)
      }
    },
    downLoadErrorMsg () {
      let downloadExcelConfig = { ...this.tempConfig }
      downloadExcelConfig.msgTypeStr = '验证结果'
      downloadExcelConfig.msg = '失败原因'
      this.hasErrorMsgData.forEach(item => {
        item.msgTypeStr = item.msgType === 1 ? '验证失败' : '验证成功'
        item.sfInStockStr = item.sfInStock ? '是' : '否'
      })
      const tHeader = utils.objectToArray(downloadExcelConfig, false)
      const filterVal = utils.objectToArray(downloadExcelConfig, true)
      exportSheet.ExportJsonToExcel({
        header: tHeader,
        filterVal,
        data: this.hasErrorMsgData,
        filename: `Excel批量导入模板`
      })
      setTimeout(() => {
        this.$notify.success({
          title: '温馨提示',
          message: `Excel批量导入模板下载成功`
        })
        this.isDisabled = false
      }, 500)
    },
    /**
     * @description: 计算出所有门店和履约时间分组中已经选中的门店
     * @param {type} 
     * @return: 
     */
    getCheckedStores (excludeIdx) {
      let allCheckedStores = []
      if (excludeIdx || excludeIdx === 0) {
        this.activityStoreGroupList.forEach((item, idx) => {
          if (idx != excludeIdx) {
            allCheckedStores = [...allCheckedStores, ...item.activityStoresCheckedList]
          }
        })
      } else {
        this.activityStoreGroupList.forEach(item => {
          allCheckedStores = [...allCheckedStores, ...item.activityStoresCheckedList]
        })
      }
      return allCheckedStores
    }

  }
}
</script>



